import React, { useContext, useState } from "react";
import { Tooltip } from "flowbite-react";

import { ThemeContext } from "../../ThemeContext";

import YoutubePlayer from "../../fragments/VideoPlayers/YoutubePlayer";
import FaceBookPlayer from "../../fragments/VideoPlayers/FaceBookPlayer";

import youtubeLightColor from "../../assets/img/youtube-light-color.svg";
import youtubeDarkColor from "../../assets/img/youtube-dark-color.svg";
import youtubeDarkGray from "../../assets/img/youtube-gray.svg";
import facebookColor from "../../assets/img/facebook-color.svg";
import facebookGray from "../../assets/img/facebook-gray.svg";

function VideoContainer(props) {
  const { isDarkMode } = useContext(ThemeContext);

  const [source, setSource] = useState("facebook");

  return (
    <>
      <div className="flex flex-wrap mb-5 sm:mb-10 ">
        <div className="w-full px-4">
          <div className="flex flex-wrap items-center justify-center">
            {source === "youtube" && !isDarkMode && (
              <img
                src={youtubeLightColor}
                role="button"
                className="h-10 me-5 hidden sm:flex"
                alt="Youtube Logo"
              />
            )}
            {source === "youtube" && isDarkMode && (
              <img
                src={youtubeDarkColor}
                role="button"
                className="h-10 me-5 hidden sm:flex"
                alt="Youtube Logo"
              />
            )}
            {source !== "youtube" && (
              <Tooltip content="View YouTube Video" placement="bottom">
                <img
                  src={youtubeDarkGray}
                  role="button"
                  className="h-10 me-5"
                  alt="Youtube Logo in Gray Color"
                  onClick={() => setSource("youtube")}
                />
              </Tooltip>
            )}

            {source === "facebook" && (
              <img
                src={facebookColor}
                role="button"
                className="h-10 ms-5 hidden sm:flex"
                alt="Facebook Logo"
              />
            )}
            {source !== "facebook" && (
              <Tooltip content="View Facebook Video" placement="bottom">
                <img
                  src={facebookGray}
                  role="button"
                  className="h-10 ms-5"
                  alt="Facebook Logo in Gray Color"
                  onClick={() => setSource("facebook")}
                />
              </Tooltip>
            )}
          </div>
        </div>
      </div>

      <div
        className="h-[170px] min-[480px]:h-[300px] sm:h-[360px] md:h-[400px] lg:h-[530px] aspect-video rounded-lg"
        ref={props.videoRef}
      >
        {source === "youtube" && (
          <YoutubePlayer isVideoVisible={props.isVideoVisible} />
        )}
        {source === "facebook" && (
          <FaceBookPlayer isVideoVisible={props.isVideoVisible} />
        )}
      </div>
    </>
  );
}

export default VideoContainer;
