import React, { Component } from "react";

import SEO from "../../fragments/components/SEO";
import NavBar from "../../fragments/components/NavBar";
import Footer from "../../fragments/components/Footer";
import Header from "../components/Header";
import HomeVideo from "../components/HomeVideo";

class LandingPage extends Component {
  render() {
    return (
      <>
        <SEO
          url="url"
          title="MPLUS"
          description="MPLUS Photography"
          name="MPLUS Photography"
          type="website"
          image="shared-image-url"
        />
        <NavBar />
        <div className="bg-white dark:bg-gray-900 pt-24">
          <main>
            <Header />
            <HomeVideo />
          </main>
        </div>
        <Footer />
      </>
    );
  }
}

export default LandingPage;
