import React, { Component, createRef } from "react";

import VideoContainer from "./VideoContainer";

class HomeVideo extends Component {
  constructor(props) {
    super(props);
    this.videoRef = createRef();
    this.observer = null;
    this.state = {
      isVideoVisible: false,
    };
  }

  componentDidMount() {
    this.observer = new IntersectionObserver(this.handleIntersection, {
      root: null,
      rootMargin: "0px",
      threshold: 0.1, // Percentage of the element visible in the viewport
    });

    if (this.videoRef.current) {
      this.observer.observe(this.videoRef.current);
    }
  }

  componentWillUnmount() {
    if (this.observer && this.videoRef.current) {
      this.observer.unobserve(this.videoRef.current);
    }
  }

  handleIntersection = (entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        this.setState({ isVideoVisible: true });
        this.observer.unobserve(entry.target); // Stop observing once the component is visible
      }
    });
  };

  render() {
    return (
      <div className="max-w-screen-xl flex mx-auto justify-center py-10">
        <div className="p-4 sm:p-4 lg:p-10 shadow-2xl dark:shadow-none bg-gray-100 dark:bg-gray-800 rounded-lg">
          <VideoContainer
            isVideoVisible={this.state.isVideoVisible}
            videoRef={this.videoRef}
          />
        </div>
      </div>
    );
  }
}

export default HomeVideo;
