import { useContext } from "react";
import "./style.scss";
import { ThemeContext } from "../../../ThemeContext";

const Switch = ({ onClick, night }) => {
  return (
    <div
      className={`shadow-lg shadow-blue-500/50 switch-wrapper ${
        !night ? "day" : "night"
      }`}
      tabIndex={-1}
      role="button"
      onClick={onClick}
    >
      <div className={`sun-moon-combo ${!night ? "sun" : "moon"}`}>
        <div className="moon-elem"></div>
        <div className="moon-elem"></div>
        <div className="moon-elem"></div>
      </div>
      <div className={`raybase ${night ? "hide" : "show"}`}>
        <div className="ray-1"></div>
        <div className="ray-2"></div>
        <div className="ray-3"></div>
      </div>
      <div className={`raybase-2 ${!night ? "hide" : "show"}`}>
        <div className="ray-1"></div>
        <div className="ray-2"></div>
        <div className="ray-3"></div>
      </div>
      {night && (
        <div className="stars">
          <div className="star"></div>
          <div className="star"></div>
          <div className="star"></div>
          {[...Array(5)].map((value, index, array) => {
            return <div key={index} className="round-star"></div>;
          })}
        </div>
      )}

      <div className="clouds-line-1" />
      <div className="clouds-line-2" />
    </div>
  );
};

const ThemeSwitch = () => {
  const { isDarkMode, toggleDarkMode } = useContext(ThemeContext);
  return (
    <div className="me-3 theme-switch">
      <Switch onClick={toggleDarkMode} night={isDarkMode} />
    </div>
  );
};

export default ThemeSwitch;
